* {
  box-sizing: border-box;
  margin: 0;
}

.wrapper {
  width: 100%;
  background: radial-gradient(circle, #eeaeca 0%, #94bbe9 100%);
  margin-top: 0;
  padding: 40px 20px;
}

input {
  max-width: 320px;
  border: 5px #010303;
  border-radius: 10px;
  padding: 20px 25px;
  box-shadow: 9px 1px 44px -7px #42445a;
}

.search-form {
  justify-content: center;
  gap: 80px;
  display: flex;
}

.search-btn {
  color: #131111;
  text-align: center;
  cursor: pointer;
  background-color: #6ea5e4;
  border: 3px #3785df;
  border-radius: 10px;
  padding: 7px 20px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
  box-shadow: 9px 1px 44px -7px #42445a;
}

.gallery-wrapper {
  margin: 0 auto;
}

.gallery {
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  gap: 50px;
  margin: 0 auto;
  padding-top: 50px;
  padding-bottom: 50px;
  display: flex;
}

.photo-card {
  max-width: 20%;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 8px 8px 24px #42445a;
}

img {
  width: 100%;
  object-fit: cover;
  height: 80%;
}

.info {
  justify-content: space-between;
  padding: 15px 15px 35px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: small;
  display: flex;
}

.load-more-btn {
  text-align: center;
  cursor: pointer;
  background-color: #6ea5e4;
  border: 3px #3785df;
  border-radius: 10px;
  margin: 0 auto 100px;
  padding: 15px 40px;
  font-size: 20px;
  display: block;
  box-shadow: inset 0 0 45px -16px #42445a;
}

